/* ===== Main Container Classes Start ===== */
.main-container {
    margin-top: 82px;
    display: flex;
  }
  
  .landing-content {
    width: 100%
  }
  
  .main-body {
    .page-wrapper {
      padding: 15px;
      transition: all ease-in 0.3s;
    }
  }
  /* ===== Main Container Classes End ===== */
  
  /* ===== Card Classes Start ===== */
  .card {
    border-radius: 5px;    
    border: 1px solid $border-color;
    margin-bottom: 10px;    
    transition: all 0.3s ease-in-out;
    box-shadow: 0 2px 6px rgba(0,0,0,.08);
  
    .card-header {
        background-color: transparent;
        border-bottom: 0px solid $border-color;
        padding: 15px 20px 15px 20px;  
        .card-header-left {
            display: inline-block;
        }  
        .card-header-right {
            border-radius: 0 0 0 7px;
            right: 20px;
            top: 9px;
            display: inline-block;
            float: right;
            position: absolute;
            @media only screen and (max-width: 575px) {
                display: none;               
            }
  
            i {
                margin: 0 15px;
                cursor: pointer;
                font-size: 20px;
                color: $primary-active-color;
                line-height: 1.4;                 
                }
            }
        }
  
        span {
            display: block;
            font-size: 12px;
            margin: 5px 0px 10px 0px;
            color: $theme-font-color2;
        }
  
      + .card-block {
          padding-top: 0;
      }
  
      h5 {
        margin-bottom: 0;
        color: $theme-dark-font-color;
        font-size: 14px;
        font-weight: 600;
        display: inline-block;
        margin-right: 10px;
        line-height: 1.4;
    }
    }
     
    .card-block {
          padding: 5px 20px 20px 20px;
         
          .card-subtitle {                
            margin: 0px 0px 10px 0px;      
          font-size: 12px; 
          font-weight: 600;
          
            i {
                vertical-align: middle;
                cursor: pointer;
                font-size: 16px; 
                color:$primary-color;
                &.icofont.icofont-spinner-alt-5 {
                    display: none;                  
                }
            }        
      }
  
        p {line-height: 1.4; }
    }
  
  /* ===== Card Classes End ===== */

.card-list-header{
    color: $theme-dark-font-color;
    font-size: 16px;
    margin-bottom: 3px;
    position:relative;
    line-height:1.5;
    .active-badge-blink {
        height: 8px;
        width: 8px;
        border-radius: 50%;
        right: -11px;
        position: absolute;
        top: -6px;
        display: inline-block !important;
        cursor: pointer;
        &:after,
        &:before {
          content: '';
          width: 100%;
          height: 100%;
          border-radius: 50%;
          position: absolute;
          top: 0;
          left: 0;
          box-shadow: inset 0 0 20px 5px rgba(42,65,232, 0.4);
        }
  
        &:after {animation: not-blink 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite;}
  
        &:before {
          animation: not-blink 2.1s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;
          animation-delay: 1.15s;
        }
      }
      .active-badge-static {
        height: 9px;
        width: 9px;
        border-radius: 50%;
        border:1px solid #ffffff;
        margin-left: 5px;
        position: absolute;
        top: -5px;
        display: inline-block !important;
        cursor: pointer;
        box-shadow: 2px 2px 5px $muted-grey;
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;          
        }  
        
      }
}

.card-list-subheader{
    color: $theme-dark-font-color;    
    margin-bottom: 3px;  
    line-height:1.5;  
    position:relative;

    .active-badge-static {
        height: 9px;
        width: 9px;
        border-radius: 50%;
        border:1px solid #ffffff;
        right: -12px;
        position: absolute;
        top: -5px;
        display: inline-block !important;
        cursor: pointer;
        box-shadow: 2px 2px 5px $muted-grey;
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;          
        }          
      }

      .card-list-icon{
        color:$primary-active-color;
        margin-right:10px;
    }
}

.card-description{
    color: $theme-dark-font-color2;
    font-size: 12px;
    margin-top: 12px;
    margin-bottom: 15px;
    .card-description-more{
        color: $primary-active-color;
        font-weight: 600;
        padding-left:10px;
        padding-right:10px;
        display: inline-block;
    }
}





