.browse-pro {
	color:rgba(0,0,0,.54);
	font:16px 'Open Sans',Helvetica,Arial,sans-serif;
	line-height:1;
	position:relative;
}

.browse-pro .browse-unit { position:relative; margin-bottom:15px; }

.browse-pro .browse-input { position:relative; }

.browse-pro label {
	display:block;
	color:inherit;
	font-weight:normal;
	text-align:left;
	margin-bottom:0;
}

/* ============== Browse Icon Start =============================== */
.browse-pro .browse-icon-left
{	color:rgba(0,0,0,.54);
	font-size:14px;
	height:25px;
	line-height:25px !important;
	opacity:.6;
	position:absolute;
	text-align:center;
	top:5px;
	width:42px;
    z-index:2;
    border-right:1px solid rgba(0,0,0,.32); left:3px;
}
.browse-pro .browse-icon-left ~ input,
.browse-pro .browse-icon-left ~ textarea { padding-left:58px; }
/* ============== Browse Icon End =============================== */

/* ==========File for upload Start =============================== */
.browse-pro .browse-file-button input {
	bottom:-1px;
	font-size:10px;
	opacity:0;
	position:absolute;
	width:90px;
	z-index:0;
}

.browse-pro .browse-append-btn .browse-file-button input { right:0; }

.browse-pro .browse-input input[type="file"] { 
	cursor:pointer; 
	font-size: 0;
}
/* ==========File for upload End =============================== */

/* ============== Browse Button Start =============================== */
.browse-pro .browse-append-btn .browse-file-button {
    border-radius:2px; 
    width:85px;
     right:4px;
     }

.browse-pro .browse-file-button {
        color:$theme-font-color;
        display:block;
        font-family:'Open Sans',Helvetica,Arial,sans-serif;
        font-size:10px;
        height:27px;
        line-height:27px;
        outline:none;
        overflow:hidden;
        position:absolute;
        text-align:center;
        top:4px;
        z-index:1;
        background-color:$default-color;
    }

.browse-pro .browse-append-btn input[type="text"] { padding-right:123px; }
/* ============== Browse Button End =============================== */

/* ============== Browse Inputs Start =============================== */
.browse-pro input[type="text"] {
	background:#fff;
	border:1px solid rgba(0,0,0,.15);	
	border-radius:3px;
	color:rgba(0,0,0,.87);
	display:block;
	font-family:inherit;
	font-size:0.813em;
	height:35px;	
	width:100%;
	outline:none;	
	appearance:none;	
	box-sizing:border-box;	
	transition:all.4s;
}
.browse-pro input[type="text"]:hover
{ border:1px solid $primary-color; }

.browse-pro input[type="text"]:focus
 { border:1px solid $primary-color; }

 .browse-selected input[type="text"] { 
	 border:1px solid $primary-color; 
}

.browse-pro .browse-input textarea { height:112px; overflow:auto; min-height:52px; resize:vertical; }

.browse-pro .browse-input textarea:focus { height:128px; }
/* ============== Browse Inputs End =============================== */