.task-progress { margin-top: 6px; }

.task-progress .progress {
    position: relative;
    background-color: #eeeded;
    height: 10px;
}

.task-progress .progress {
    margin-bottom: 6px;
}

.task-progress .progress .task-bar-success,
.task-progress .progress .task-bar-danger,
.task-progress .progress .task-bar-warning
{
    background: #29aecc;
    height: 10px;
    border-radius: 0;
    position: absolute;
    top: 0;
}

.task-progress .progress .task-bar-success {
    background-color: $success-color;
}

.task-progress .progress .task-bar-danger {
    background-color: $danger-color;
}

.task-progress .progress .task-bar-warning {
    background-color: $warning-color;
}