.home-dropdown-caret{
    background-color: #f0f0f0;
    color: #a8a8a8;
    border-radius: 3px;
    margin-left: 7px;
    margin-bottom: 3px;
    padding:3px 5px;
    font-size: 12px;
    &:hover {        
        color: $primary-active-color !important;
        background-color: $light-blue-color;
      }
}
 /* dd-select overides */
 .dd-wrap > .dd__menu {
    margin: 0px;
    min-width: max-content;
    width: max-content;
    z-index: 200;
}
.dd-wrap > .dd__menu > .dd__option {
    padding: 4px;
}
.dd-wrap > .dd__control {
    min-height: 0px;
    border-radius: 2px;
}
.dd-wrap > .dd__control--is-focused {
    min-height: 0px;
    box-shadow: none;
}
.dd-wrap > .dd__control > .dd__value-container {
    padding: 0px 6px;
}
.dd-wrap > .dd__control > .dd__indicators > .dd__dropdown-indicator {
    padding: 4px;
}
.dd-wrap > .dd__control > .dd__indicators > .dd__clear-indicator {
    padding: 4px;
}
.dd__multi-value {
    width: max-content;
}

/* extDD Styles */

.dropdown-menu {
    font-size: inherit;
    padding: 14px 0px;
    min-width: max-content;
    margin-top: 10px;
    border:0px;
    background-color: $theme-dark-color;
    border-radius: 4px;
    font-size: 15.7px;
}

.dropdown-item {
    border: none;
    color: #aaa;
    cursor:pointer;
    padding: 6px 40px 6px 15px!important;
    &:hover {
        color: #fff;
        background: transparent;
    }
    &:focus{
        color: #ffffff;
    text-decoration: none;
    background-color: transparent;
    outline:none;
    }
    &:active{
        color: #ffffff;
    text-decoration: none;
    background-color: transparent;
    outline:none;
    }
}
.dd-item {
    padding: 1px 5px;
    /*border-bottom: 1px solid #dad9d9; $color-lite-blue */
}

/* extDD Styles End */