.icon-segment i {
    border: 1px solid $default-color;
    background-color: $c-x2l-grey;
    border-radius: 3px;
    color:$muted-grey;
    display: inline-block;
    font-size: 16px;
    height: 40px;
    line-height: 40px;
    margin: 0 12px 0 0;
    text-align: center;
    vertical-align: middle;
    width: 40px;
    cursor: pointer;
}

.icon-segment i:hover {
    color: $primary-color;
    background: lighten($primary-color, 30%);
}