.link-footer {
    margin-left: 0;
    margin-right: 0;

    > div {
        padding: 15px 0;
        text-align: center;
        cursor: pointer;
        transition: all 0.3s ease-in-out;

        &:hover {
            background-color: #f1f1f1,;            
            color:$primary-color;
        }
    }
}