/* Master Variables */
// Named FONT
$theme-font-family: 'Poppins', 'Audiowide', sans-serif;
$theme-font-size: 14px;

.f-8{font-size: 8px}
.f-10{font-size: 10px}
.f-14{font-size: 14px}
.f-12{font-size: 12px}

/* Overrides */
.row{  
  margin-right: -5px;
  margin-left: -5px;
}

.col-md-1, .col-md-2, .col-md-3, .col-md-4,.col-md-5,.col-md-6, 
.col-md-7,.col-md-8,.col-md-9,.col-md-10,.col-md-11,.col-md-12 {
  padding-right: 5px;
  padding-left: 5px;
}

body {
    background-color: $theme-bg-color;
    font-size: $theme-font-size;
    overflow-x: hidden;
    color: $theme-dark-font-color2;
    font-family: $theme-font-family;
  }

  ul {
    padding-left: 0;
    list-style-type: none;
    margin-bottom: 0;
  }
  
  * {
    &:focus {
      outline: none;
    }
  }
  
  a {
    font-size: 13px;
    cursor: pointer;
    @media only screen and(min-width: 1400px) {
      font-size: 14px;
    }
    color: $inverse-color;
  
    &:focus,
    &:hover {
      text-decoration: none;
      color: $primary-color;
    }
  }

  /* ===== Paragraph Classes Start ===== */
  p { font-size: 14px; }
  
  h1, h2, h3, h4, h5, h6 {font-weight: 600;}

  h6 {font-size: 14px;}

  hr {
    margin: 0px 20px 15px 0px;
    border: 0;
    border-top: 0.75px solid $border-color;
  }
  /* ===== Paragraph Classes End ===== */

  /*====== Font-weight css starts ======*/

$i: 500, 550, 600, 700, 900;
@each $val in $i {
  .f-w-#{$val} {
    @if ($val==900) {
      font-weight: 600;
    } @else {
      font-weight: $val;
    }
  }
}
/*====== Font-weight css ends ======*/

/*====== Font-size css starts ======*/
$i:8;
@while $i<=16 {
    .f-s-#{$i} {
        font-size:#{$i}px;
    }
    $i:$i+2;
}
/*====== Font-size css ends ======*/

/*====== Vertical-Align css starts ======*/
.middle {
  margin: auto;
  width: 50%;  
  padding: 3px 10px 3px 10px;
}
/*====== Vertical-Align css ends ======*/

/*====== Text-align css starts ======*/
$i: center, left, right;
@each $val in $i {
  .text-#{$val} {
    text-align: $val;
  }
}
/*====== Text-align css ends ======*/

  /* ===== Background and Text Color Css Start ===== */
  @each $value in $color-name {
    $i: index($color-name, $value);
  
    .bg-#{$value} {
      background: nth($color-color, $i);
    }

    .text-#{$value} {
      color: nth($color-color,$i);
  }
  }
  /* ===== Background and Text Color Css End ===== */

  /*======= All-Borders-color css starts  ======= */

$b-name:primary,
primary-active,
warning,
default,
border,
danger,
success,
inverse,
info,
x1l-default,
x2l-default,
x1d-default;
$b-color:$primary-color,
$primary-active-color,
$warning-color,
$default-color,
$border-color,
$danger-color,
$success-color,
$inverse-color,
$info-color,
$c-x1l-grey,
$c-x2l-grey,
$c-x1d-grey;
@each $var in $b-name {
    $i: index($b-name, $var);
    .b-#{$var} {
      border: 1px solid nth($b-color, $i);
  }
    .b-t-#{$var} {
        border-top: 1px solid nth($b-color, $i);
    }
    .b-b-#{$var} {
        border-bottom: 1px solid nth($b-color, $i);
    }
    .b-l-#{$var} {
        border-left: 1px solid nth($b-color, $i);
    }
    .b-r-#{$var} {
        border-right: 1px solid nth($b-color, $i);
    }
}
/*======= All-Borders-color css ends  ======= */

/*====== Float css starts ======*/
$i:left,
right,
none;
@each $val in $i {
    .f-#{$val} {
        float: $val;
    }
}
/*====== Float css ends ======*/

.c-pointer { cursor: pointer;}
.img-radius-50 { border-radius: 50%;  }



/*====== Margin & Padding  ======*/
$i: 0;
@while $i<=50 {
    /* Margin */
    .mg-#{$i} { // All
        margin: #{$i}px !important;
    }
    .mg-t-#{$i} { // Top 
        margin-top: #{$i}px !important;
    }
    .mg-b-#{$i} { // Bottom
        margin-bottom: #{$i}px !important;
    }
    .mg-l-#{$i} { // Left
        margin-left: #{$i}px !important;
    }
    .mg-r-#{$i} {
        margin-right: #{$i}px !important;
    }
    /* Padding */
    .pd-#{$i} {
        padding: #{$i}px !important;
    }
    .pd-t-#{$i} {
        padding-top: #{$i}px !important;
    }
    .pd-b-#{$i} {
        padding-bottom: #{$i}px !important;
    }
    .pd-l-#{$i} {
        padding-left: #{$i}px !important;
    }
    .pd-r-#{$i} {
        padding-right: #{$i}px !important;
    }
    $i: $i+5;
}

/*======= Height & Width =================*/
$i: 0;
@while $i<=1000 {
    .wd-#{$i} {
        width: #{$i}px !important;
    }
    .ht-#{$i} {
        height: #{$i}px !important;
    }
    $i: $i+10;
}
/*========== PCT Height & Width ==================*/
$i: 0;
@while $i<=100 {
    .wd-pct-#{$i} {
        width: round(percentage($i/100)) !important;
    }
    .ht-pct-#{$i} {
        height:round(percentage($i/100));
    }
    $i: $i+1;
}

.wd-pct-12h{
    width:12.5%;
}


/*======== line-height in px ==================*/
$i: 0;
@while $i<=40 {
    .ln-ht-#{$i} {
        line-height: #{$i}px;
    }
    $i: $i+1;
}

.v-align{    
  display: flex;
  align-items: center;
}

.v-align-bottom{    
    display: flex;
    align-items: baseline;
  }

.box-shadow{
    box-shadow: 0 2px 6px rgba(0,0,0,.08);
}

.box-shadow-top{
    box-shadow: 0 -2px 6px rgba(0,0,0,.08);
}

/*====== Text-align css starts ======*/
$i: center, left, right;
@each $val in $i {
  .text-#{$val} {
    text-align: $val;
  }
}
/*====== Text-align css ends ======*/

/*====== Text-styles css starts ======*/
.text-small-muted{
    font-size: 12px;
    color: $theme-font-color2;
}

.text-small-dark{
    font-size: 12px;
    color: $theme-dark-font-color;
    font-weight: 500;
}

.text-small-primary{
    font-size: 12px;
    color: $primary-active-color;
    font-weight: 500;
}

.text-dark{
    color: $theme-dark-font-color;
}

.mandatory-field{
    font-size: 14px;
    color:$primary-active-color;
    margin-left: 3px;
}