.btn {
    border-radius: 2px;
    text-transform: capitalize;
    font-size: 12px;
    padding: 5px 17px;    
    cursor: pointer;

    &.btn-icon {
        border-radius: 50%;
        width: 40px;
        line-height: 30px;
        height: 40px;
        padding: 3px;
        text-align: center;
    }
}

.btn-small {    
    padding: 4px 15px;
    font-size: 11px;
}

.btn-mini {    
    padding: 3px 10px;
    font-size: 10px;
}

.btn-round{border-radius:50%;}

.btn.btn-disabled {cursor: not-allowed;}

.btn-neutral {
    color:$default-color;
    padding: 0px 10px; 
    &:hover {        
        color: $muted-grey;
      } 
}

.btn-selected {
    color:$success-color;
    padding: 0px 10px;
}

.button-info {
    background: rgba(255,255,255,.25);
    color: #fff;
    width: 19px;
    height: 19px;
    display: inline-block;
    text-align: center;
    line-height: 19px;
    border-radius: 50%;
    font-size: 11px;
    font-weight: 700;
    margin-left: 5px;
    margin-right: -2px;
}

/**====== Basic-Buttons css Start ======**/
.btn-primary {
    @include buttons(theme-color(primary, base), theme-color(primary, base));

    &:hover {
        @include buttons-hover(theme-color(primary, hover), theme-color(primary, hover));
    }

    &:active {
        @include buttons-active(theme-color(primary, active), theme-color(primary, active));
    }

    &:focus {
        @include buttons-focus(theme-color(primary, hover));
    }

    &.disabled {
        @include buttons-trans(theme-color(primary, trans), theme-color(primary, trans));
        color: #ffffff;
    }
}

