.md-tabs {
    position: relative;
  
    .nav-item+.nav-item {
        margin: 0;
    }
  
    .nav-link {
        border: none;
        color: $inverse-color;
        cursor: pointer;
    }
  
    .nav-item {
        width: calc(100% / 5);
        text-align: center;
    }
  
    .nav-link:focus,
    .nav-link:hover {
        border: none;
    }
  
    .nav-item .nav-link.active~.slide {
        opacity: 1;
        transition: all 0.3s ease-out;
    }
  
    .nav-item .nav-link~.slide {
        opacity: 0;
        transition: all 0.3s ease-out;
    }
  
    .nav-item.open .nav-link,
    .nav-item.open .nav-link:focus,
    .nav-item.open .nav-link:hover,
    .nav-link.active,
    .nav-link.active:focus,
    .nav-link.active:hover {
        color: $primary-color;
        border: none;
        background-color: transparent;
        border-radius: 0;
    }
  
    .nav-item a {
        padding: 20px 0 !important;
        color: $inverse-color;
    }
  }
  
  .nav-tabs .slide {
    background: $primary-color;
    width: calc(100% / 5);
    height: 4px;
    position: absolute;
    -webkit-transition: left 0.3s ease-out;
    transition: left 0.3s ease-out;
    bottom: 0;
  
    .nav-item.show .nav-link,
    .nav-link {
        color: $primary-color;
    }
  }