.nav-navigate {
  font-size: 16px !important; 
  padding: 7px 9px;
  margin: 0px 5px;
  &:hover {        
      color: $primary-active-color !important;
      cursor: pointer;
    }
}

.header-navbar {
    background: linear-gradient(to right, $header-theme, $header-right-theme);
    display: block;
    position: fixed;
    align-items: center;
    height: 82px;
    box-shadow: 0 0 15px 0 rgba(0,0,0,.12);       
    flex-wrap: wrap;
    justify-content: space-between;     
    z-index: 800; 
    width: 100%;  

    .navbar-container {
      margin-left: auto;
      .header-notification {
        perspective: 1000px;
        z-index: 99; 
        
          li {
            padding: 0.7em 20px;
            line-height: initial;
            width: 100%;
            transition: all 0.3s ease-in-out;
            &:hover {
              background-color: #f1f1f1;
            }
          }
  
          a {
            font-size: 15px;
            padding-left: 0;
            color: $theme-font-color;
          }
  
          i {
            font-size: 15px;
            margin-left: 0;
            margin-right: 5px;
          }        
      }
  
      .active::before {
        content: '';
        border: 10px solid transparent;
        border-left-color: #fff;
        border-top-color: #fff;
        position: absolute;
        border-radius: 5px;
        bottom: -32px;
        right: 20px;
        box-shadow: -11px -11px 35px 0 rgba(0, 0, 0, 0.25);
        transform: rotate(45deg);
        z-index: 9;
      }
      .header-badge {
        height: 19px;
        width: 19px;
        line-height: 19px; 
        text-align: center;
        color: #fff;
        font-size: 11px;
        border-radius: 50%;
        right: 35px;
        position: absolute;
        top: 5px;
        display: inline-block !important;
        cursor: pointer;
        &:after,
        &:before {
          content: '';
          width: 100%;
          height: 100%;
          border-radius: 50%;
          position: absolute;
          top: 0;
          left: 0;
          box-shadow: inset 0 0 20px 5px rgba(255, 255, 255, 0.4);
        }
  
        &:after {
          animation: not-blink 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite;
        }
  
        &:before {
          animation: not-blink 2.1s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;
          animation-delay: 1.15s;
        }
      }
    }
    
    .nav-right {
      float: right;
      li {
        float: left;
        padding: 0 10px;
        position: relative;  
        text-align: center;
        width:110px;
        a {font-size: 20px;}
      }
      a {
        padding: 0 0.6rem;
      }
      .user-profile {
        cursor: pointer;
        i {
          font-size: 10px;
        }
        img {
          margin-right: 10px;
          width: 40px;          
          background: $default-color;
        }
      }
    }
  }
  
@keyframes not-blink {
    0% {
      transform: scale(1);
      opacity: 1;
    }
  
    100% {
      transform: scale(4);
      opacity: 0;
    }
  }

  .navbar-logo {
    position: relative;
    align-items: center;
    display: flex;
    float: left;
    height: 82px;
    text-align: center;
    border-right: 1.5px solid #e8e9f2;
    color:$inverse-color !important;
    font-size:30px !important;
    width: 220px;
    padding: 10px;
}

.collapse-menu {
    position: absolute;
    right: 0px;
    top: calc(50% - 8px);
    font-size: 16px;
    cursor: pointer;
    padding-right: 10px;
  }
