.ms-container{
    border: 1px solid $c-x1l-grey;
}

.ms-list {   

    li {
        width: 100%;
        padding:2px 5px;
        display: block; 
        border-bottom: 0.75px solid $c-x1l-grey;      

        &:focus,
        &:hover {
        background-color: lighten($primary-color, 30%);        
        cursor:pointer;
    }
    }

    i {
        font-size: 12px;
        color: lighten($success-color, 5%);  
    }
}

