.modal-wrap {
    width: 700px; // Default width of the popup
    margin: 1.75rem auto; // Auto for horizontal center, and 100px is magin from top.
    // top: 50%; // ------------> For vertical Center
    // -ms-transform: translateY(-50%);
    // transform: translateY(-50%);
}
.modal-dialog {
    max-width: unset;
}
.modal-content {
    border-radius: 5px;    
    border: 1px solid $border-color;
    transition: all 0.3s ease-in-out;
    box-shadow: 0 2px 6px rgba(0,0,0,.08);
    outline: 0;
    }

.modal-header {
    margin-bottom: 0;
    color: $theme-dark-font-color;
    font-size: 16px;
    font-weight: 600;
    display: inline-block;
    padding: 0px;
    border-bottom: none;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
}

.modal-subheader {
    margin-bottom: 0;
    color: $theme-dark-font-color2;
    font-size: 14px;
    margin-bottom: 5px;
    padding: 0px;
    border-bottom: none;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
}

.modal-sublistheader {
    color: $theme-dark-font-color;
    font-weight:600;
    font-size: 12px;
    display: inline-block;
    margin: 7px 0px 0px 0px;
    padding: 0px;
    border-bottom: none;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
}

.modal-close {
    margin-bottom: 0;
    color: $primary-active-color;
    font-size: 16px;
    font-weight: 600;
    display: inline-block;
    padding: 0px;
}

.modal-block {
    padding: 5px 20px 20px 20px;
}

.textarea{
    border: 1px solid $border-color;
    line-height: 27px;
    padding: 10px;
    min-height: 130px;
    transition: none!important;
    min-width: 100%;
    outline: none;
    font-size:12px;
    color:$theme-dark-font-color2;
    margin: 0 0 16px;
    width: 100%;
    display: block;
    background-color: #fff;    
    border-radius: 4px;
}



