/* Named Colors */

$header-theme: #ffffff;
$header-right-theme: #ffffff;
$theme-bg-color: #fafafd;
$theme-dark-color: #303030;
$theme-font-color: #5e5f67;
$theme-font-color2: #c5c6c6;
$theme-dark-font-color: #333;
$theme-dark-font-color2: #888;

/* Master Colors */
$primary-color: #2a41e8;
$primary-lite-color: #eceef9;
$primary-active-color: #2a41e8;
$secondary-color: #a8d1ff;
$warning-color: #ffb64d;
$warning-lite-color: #fbf6dd;
$default-color: #c5c6c6;
$default-lite-color: #f2f2f2;
$danger-color: #ff5370;
$danger-lite-color: #ffe9e9;
$success-color: #4d9c30;
$success-lite-color: #ebf6e0;
$inverse-color: #222;
$info-color: #00bcd4;
$disabled-color:#4099ff;
$white-color: #ffffff;
$blue-grey-color: #2c3e50;
$muted-grey: #919aa3;
$silver-color:silver;
$light-blue-color: #e9ebfd;
$border-color: #e0e0e0;

/* Elements color */
$color-name:  c-blue, c-primary-active, c-bluegreen, c-lite-blue, c-success, c-warning, c-red, 
              c-default-grey, c-white, c-blue-grey, c-muted-grey;
$color-color: #4099ff, #2a41e8, #00bcd4, #a8d1ff, #4d9c30, #ffb64d, #ff5370,
              #d6d6d6, #ffffff,  #2c3e50, #919aa3;

$theme-color: (
  primary: (
    base: $primary-color,
    hover: $theme-dark-color,
    active: darken($primary-color, 12%),
    trans: transparentize($primary-color, 0.5),
  ),
  primary-outline: (
    base: $primary-color,
    hover: $primary-color,
    active: darken($primary-color, 12%),
    trans: transparentize($primary-color, 0.5),
  ),
  primary-lite: (
    base: lighten($primary-active-color, 42%),
    hover: lighten($primary-active-color, 42%),
    active: lighten($primary-active-color, 42%),
    trans: transparentize(lighten($primary-active-color, 42%), 0.5),
  ),
  secondary: (
    base: $secondary-color,
    hover: lighten($secondary-color, 10%),
    active: darken($secondary-color, 12%),
    trans: transparentize($secondary-color, 0.5),
  ),
  warning: (
    base: $warning-color,
    hover: lighten($warning-color, 10%),
    active: darken($warning-color, 12%),
    trans: transparentize($warning-color, 0.5),
  ),
  default: (
    base: $default-color,
    hover: $theme-dark-color,
    active: darken($default-color, 12%),
    trans: transparentize($default-color, 0.5),
  ),  
  default-lite: (
    base: $default-lite-color,
    hover: $default-lite-color,
    active: $default-lite-color,
    trans: transparentize($default-lite-color, 0.5),
  ),  
  danger: (
    base: $danger-color,
    hover: lighten($danger-color, 10%),
    active: darken($danger-color, 12%),
    trans: transparentize($danger-color, 0.5),
  ),
  danger-lite: (
    base: $danger-lite-color,
    hover: $danger-lite-color,
    active: $danger-lite-color,
    trans: transparentize($danger-lite-color, 0.5),
  ),
  success: (
    base: $success-color,
    hover: lighten($success-color, 10%),
    active: darken($success-color, 12%),
    trans: transparentize($success-color, 0.5),
  ),
  success-lite: (
    base: $success-lite-color,
    hover: $success-lite-color,
    active: $success-lite-color,
    trans: transparentize($success-lite-color, 0.5),
  ),
  inverse: (
    base: $inverse-color,
    hover: lighten($inverse-color, 10%),
    active: darken($inverse-color, 12%),
    trans: transparentize($inverse-color, 0.5),
  ),
  info: (
    base: $info-color,
    hover: lighten($info-color, 10%),
    active: darken($info-color, 12%),
    trans: transparentize($info-color, 0.5),
  ),
  disabled: (
    base: $disabled-color,
    hover: lighten($disabled-color, 10%),
    active: darken($disabled-color, 12%),
    trans: transparentize($disabled-color, 0.5),
  ),
);
@function theme-color($color-name, $color-variant) {
  @return map-get(map-get($theme-color, $color-name), $color-variant);
}

/* Variations */
$c-x1l-grey: lighten($default-color, 5%);
$c-x2l-grey: lighten($default-color, 10%);
$c-x1d-grey: darken($default-color, 5%);
$c-x2d-grey: darken($default-color, 10%);
$c-x3d-grey: darken($default-color, 20%);

/* Dissociation - Do not use master color and variations directly when colors may change on iteration, 
instead dissociate into variables before using.
This will feature one shot change, from this file it self */

$common-font-color: $c-x3d-grey;

/* Export is for Using colors in JS directly */
:export {
  red: $danger-color;
}