.checkbox-fade,
.checkbox-zoom {
  display: inline-block;
  margin-right: 15px;

  label {
    line-height: 20px;    
    margin-bottom: 0px;

    input[type='checkbox'],
    input[type='radio'] {
      display: none;

      &:checked + .cr > .cr-icon {
        transform: scale(1) rotateZ(0deg);
        opacity: 1;
      }

      + .cr > .cr-icon {
        transform: scale(3) rotateZ(-20deg);
        opacity: 0;
        transition: all 0.3s ease-in;
      }
    }

    &:after {
      content: '';
      display: table;
      clear: both;
    }
  }

  &.fade-in-disable {
    .cr,
    label {
      color: #ccc;
      cursor: no-drop;
    }
  }

  .cr {
    border-radius: 0;
    border: 2px solid $primary-color;
    cursor: pointer;
    display: inline-block;
    float: left;
    height: 18px;
    margin-right: 0.5em;
    position: relative;
    width: 18px;

    .cr-icon {
      color: $primary-color;
      font-size: 0.8em;
      left: 0;
      line-height: 0;
      position: absolute;
      right: 0;
      text-align: center;
      top: 50%;
    }
  }
}
.checkbox-fade {
  label {
    line-height: 20px;

    input[type='checkbox'],
    input[type='radio'] {
      display: none;

      &:checked + .cr > .cr-icon {
        transform: scale(1) rotateZ(0deg);
        opacity: 1;
      }

      + .cr > .cr-icon {
        transform: scale(3) rotateZ(-20deg);
        opacity: 0;
        transition: all 0.3s ease-in;
      }
    }
  }
}
.checkbox-zoom {
  label {
    line-height: 20px;
    input[type='checkbox'],
    input[type='radio'] {
      display: none;

      &:checked + .cr > .cr-icon {
        transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
        opacity: 1;
      }

      + .cr > .cr-icon {
        transform: scale3d(0.2, 0.2, 0.1) translate3d(0, 0, 0);
        opacity: 0;
        transition: all 0.3s ease-in;
      }
    }
  }
}

$fade-in-checkbox-bg-state: primary, warning, danger, success, info;
$fade-in-checkbox-bg-color: $primary-color, $warning-color, $danger-color, $success-color, $info-color;
@each $var in $fade-in-checkbox-bg-state {
  $i: index($fade-in-checkbox-bg-state, $var);

  .checkbox-fade,
  .checkbox-zoom {
    &.fade-in-#{$var},
    &.zoom-#{$var} {
      .cr {
        border: 2px solid nth($fade-in-checkbox-bg-color, $i);
        .cr-icon {
          color: nth($fade-in-checkbox-bg-color, $i);
        }
      }
    }
  }  
}