.home-section {
    display: block;
}

.home-headline{
    display: block;
    position: relative;
}

.home-headline.center {
    text-align: center;
    padding: 0;
}

.home-headline h3{
    font-size: 26px;
    line-height: 36px;
    font-weight: 500;
    color: $inverse-color;
}

.categories-container {
    display: flex;
    flex-wrap: wrap;
}

.category-box {
    flex: 0 0 25%;
    align-content: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
    margin: 0;
    text-align: center;
    padding: 25px;
    border-radius: 4px;
    transition: .35s;
}

.category-box-icon {
    font-size: 42px;
    line-height: 32px;
    color:$primary-active-color;
    position: relative;

    i {
        height: 42px;
        display: block;
        transition: .35s;
        margin-bottom: 12px;
    }
}

.category-box-counter {
    width: auto;
    height: 24px;
    font-size: 14px;
    line-height: 25px;
    border-radius: 4px;
    padding: 0 8px;
    color: #909090;
    background: rgba(0,0,0,.06);
    font-weight: 600;
    display: inline-block;
    margin: 0 auto;
    transition: .35s;
    margin-bottom: 18px;
}

.category-box-content h3 {
    font-size: 16px;
    font-weight: 600;
    color: $theme-dark-font-color;
    margin: 0;
    padding: 0;
    transition: .35s;
}

.category-box-content p {
    color: $theme-dark-font-color2;
    margin: 0;
    padding: 0;
    line-height: 24px;
    margin-top: 5px;
    transition: .35s;
}

/* Home Banner Start */
.banner-container {
    position: relative;    
    color: white;
  }

  .banner-top-left {
    position: absolute;
    top: 140px;
    left: 180px;
    width:80%;    
    margin-right: auto;
    margin-left: auto;
  }

  .banner-headline {
    display: block;
    max-width: 55%;

    .banner-dark-overlay{
        color: rgba(255,255,255,.9);
        font-size: 26px;
    line-height: 42px;
    
    }
}

/* Home Search Styles Start */
.home-search {
    display: block;
    max-width: 100%;
    margin-top:30px;
}

.intro-banner-search-form {
    display: flex;
    width: 100%;
    background-color: #fff;
    border-radius: 4px;

    input{font-size: 18px;
        margin: 0;
        box-shadow: none;
        border:0px;  
        padding: 10px 15px;
        padding-left: 43px;    }

    .input-with-icon {
        align-self: center;
        flex: 1;
        display: block;
    position: relative;

     i {
        position: absolute;
        right: auto;
        left: 10px;
        top: calc(25% - 1px);
        font-size: 24px;
        color: #b0b0b0;
    }   
    }

    .intro-search-field {
        padding: 10px 12px;
        border-right: 1px solid #e4e4e4;
        flex: 1;
        align-items: center;
        display: flex;
        position: relative;    
    
    .field-title{
        background:$primary-active-color;
        position: absolute;
        top: 0;
        left: 0;
        color: #fff;
        border-radius: 4px;
        transform: translateY(calc(-100% - 15px));
        cursor: default;
        font-size: 16px;
        padding: 6px 10px;
        line-height: 22px;
        margin-right: 10px;
        font-weight: 400; 
    }
    
    .bootstrap-select.btn-group button {
        padding: 0 15px;
        box-shadow: none;
        font-size: 18px;
        position: relative;
        height: 48px;
        line-height: 0;
        margin: 0;
        width: 100%;
        display: block;
        font-weight: 500;
        opacity: 1;
        border-radius: 4px;
        border: none;
    }
        }


}

.bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
        width: 100%;
    }
.bootstrap-select.btn-group:not(.input-group-btn), .bootstrap-select.btn-group[class*=col-] {
        float: none;
        display: flex;
        margin-left: 0;
        position: relative;
    }
 

    .intro-search-button {
        margin: 8px;
        margin-left: 0;
        flex: auto;
        flex-grow: 0;
        display: flex;
        align-items: center;

        .button {
            height: 52px;
            line-height: 52px;
            padding: 0 30px;
            font-size: 18px;
            background-color: $primary-active-color;
            top: 0;
            color: #fff;
            position: relative;
            font-weight: 500;
            display: inline-block;
            transition: all .2s ease-in-out;
            cursor: pointer;
            overflow: hidden;
            border: none;
            border-radius: 4px;
        }
    }

/* Home Stats Styles Start */
    .intro-stats {
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;

        li {
            border-right: 1px solid rgba(255,255,255,.25);
            flex: auto;
    flex-grow: 0;
    margin-right: 35px;
    padding-right: 35px;

    strong {
        font-weight: 600;
        color: #fff;
        font-size: 28px;
        margin-bottom: 2px;
        display: block;
    margin: 0;
    padding: 0;
    }
    
    span{
        color: rgba(255,255,255,.7);
        font-weight: 300;
        font-size: 18px;
        display: block;
    margin: 0;
    padding: 0;
    }
        }

        li:last-child{border-right:none;}
    }

/* Home Footer Styles Start */

.home-footer {
    background-color: $theme-dark-color;
    color: $silver-color;

    .home-footer-links  {   
    h3 {
        font-size: 18px;
        color: #fff;
        margin: 0 0 15px;
        padding: 0;
        display: block;
        font-weight:500;
    }

    ul {
        list-style: none;
        text-align: left;
        margin: 0;
        padding: 0;

        li:first-child a {
            padding-top: 0;
        }

        li {
            margin-bottom: 0;
            display: block;
           
             a {
                color: silver;
                display: block;
                line-height: 25px;
                position: relative;
                z-index: 100;
                padding: 4px 0;
            }
        }
        }
    }

    p {
        line-height: 31px;
    }

}

.home-footer-top-section {
    border-bottom: 1px solid #484848;    
}

.home-footer-row-container {
    display: block;
    height: 96px;
    position: relative;
    z-index: 100;
}

.home-footer-row {
    display: inline-block;
    padding: 0 40px;    
    float: left;
    height: 100%;
     
}

.home-footer-row-inner {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    align-items: center;
    text-align: center;  
    height: 96px; 
}

.home-footer-logo {
    transform: translateY(0);
    position: relative;
    height: 96px;    
    align-items: center;
    display: flex;
    float: left;    
    text-align: center;    
    font-size:25px !important;
    color: $theme-font-color2;

    img {
        max-height: 46px;
        vertical-align: middle;
        padding-right:10px;
    }
}

.home-footer-row-right {
    float: right;
    height: 100%;
    padding-left: 40px;
    border-left: 1px solid #484848;
}

.home-footer-social-links {
    list-style: none;
    margin: 0 -10px;
    margin-top: 3px;
    padding: 0;

    li {
        display: inline-block;
        padding: 0;
        margin: 0;
        float: left;

        a {
            font-size: 20px;
            padding: 0 10px;
            color: #fff;
            display: inline-block;
            transition: .3s;
            opacity: .5;
            transform: translate3d(0,-0.5px,0);
        }
    }
}

.home-footer-middle-section {
    padding: 50px 0;
    position: relative;
    z-index: 10;
}

.newsletter {
    display: flex;
    margin-top: 20px;
    input {
    background: #262626;
    box-shadow: none;
    margin-bottom: 0;
    height: 48px;
    line-height: 48px;
    padding: 0 20px;
    outline: none;
    font-size: 16px;
    color: gray;
    margin: 0 0 16px;
    max-width: 100%;
    width: 100%;
    box-sizing: border-box;
    font-weight: 500;
    opacity: 1;
    border-radius: 4px;
    border: none;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
}


}
