@mixin buttons($back-color,$border-color,$color:#fff,$cursor:pointer,$transition:all ease-in 0.3s){
	background-color: $back-color;
	border-color:$border-color;
	color:$color;
	cursor:$cursor;
	transition: $transition;	
}

@mixin buttons-hover($back-color,$border-color,$color:#fff){
	background-color: $back-color;
	border-color:$border-color;
	color:$color;
}

@mixin buttons-active($back-color,$border-color,$color:#fff,$box-shadow:none){
	background-color: $back-color !important;
	border-color:$border-color;
	box-shadow:$box-shadow;
	color:$color;
}

@mixin buttons-trans($back-color,$border-color){
	background-color:$back-color;
	border-color:$border-color;
}

@mixin buttons-focus($back-color,$color:#fff,$box-shadow:none){
	box-shadow:$box-shadow;
	color:$color;
	background-color:$back-color;
}

@mixin buttons-borders($color,$back-color:#fff){
	color:$color;
	background-color:$back-color;
}




@mixin buttons-lite($back-color,$border-color,$color:darken($back-color,50%),$cursor:pointer,$transition:all ease-in 0.3s){
	background-color: $back-color;
	border-color:darken($back-color,20%);
	color:$color;
	cursor:$cursor;
	transition: $transition;
}

@mixin buttons-lite-hover($back-color,$border-color,$color:darken($back-color,50%)){
	background-color: $back-color;
	color:darken($back-color,60%);
	border-color:darken($back-color,50%);
}

@mixin buttons-lite-active($back-color,$border-color,$color:darken($back-color,50%),$box-shadow:none){
	background-color: $back-color !important;
	border-color:darken($back-color,50%);
	box-shadow:$box-shadow;
	color:$color;
}

@mixin buttons-lite-trans($back-color,$border-color){
	background-color:$back-color;
	border-color:darken($back-color,20%);
}

@mixin buttons-lite-focus($back-color,$color:darken($back-color,50%),$box-shadow:none){
	box-shadow:$box-shadow;
	color:$color;
	background-color:$back-color;
}

@mixin buttons-lite-borders($color,$back-color:#fff){
	color:$color;
	background-color:$back-color;
}