.label {
    border-radius: 4px;
    font-size: 10px;
    padding: 4px 7px;
    line-height: 14px;
    margin-right: 10px;
    color: #fff ;
}

$label-name:primary,
warning,
default,
danger,
success,
inverse,
info;
$label-color:$primary-color,
$warning-color,
$default-color,
$danger-color,
$success-color,
$inverse-color,
$info-color;
@each $var in $label-name {
    $i: index($label-name, $var);
    .label-#{$var} {
        background:nth($label-color,$i);
    }
}

$label-name:primary-lite,
warning-lite,
default-lite,
danger-lite,
success-lite,
info-lite;
$label-color:lighten($primary-active-color,40%),
$warning-lite-color,
$default-lite-color,
$danger-lite-color,
$success-lite-color,
lighten($info-color,40%);

$label-font:$primary-active-color,
$warning-color,
$theme-font-color,
$danger-color,
$success-color,
$info-color;
@each $var in $label-name {
    $i: index($label-name, $var);
    .label-#{$var} {
        background:nth($label-color,$i);
        color:nth($label-font,$i);
    }
}