.form-control {
    font-size: 12px;    
    border-radius: 3px;
    border:1px solid $border-color;
    padding:.5rem .75rem;
    line-height: 1.25;

    &:focus {
        border: 1px solid $primary-active-color;
        box-shadow: none;
    }

    &.form-control-normal {
        font-weight: 400;
    }

    &.form-control-bold {
        font-weight: 600;
    }

    &.form-control-capitalize {
        text-transform: capitalize;
    }

    &.form-control-uppercase {
        text-transform: uppercase;
    }

    &.form-control-lowercase {
        text-transform: lowercase;
    }

    &.form-control-variant {
        font-variant: small-caps;
    }

    &.form-control-left {
        text-align: left;
    }

    &.form-control-center {
        text-align: center;
    }

    &.form-control-right {
        text-align: right;
    }

    &.form-control-rtl {
        direction: rtl;
    }

    &:disabled {
        cursor: not-allowed;
        opacity: 0.5;
    }
}

.input-group-addon:not(:first-child){
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}