
$border-color:$primary-color,
$warning-color,
$default-color,
$danger-color,
$success-color,
$inverse-color,
$info-color;

/*======= Card-Border-Top-color starts  ======= */
$card-name:primary,
warning,
default,
danger,
success,
inverse,
info;
@each $var in $card-name {
    $i: index($card-name, $var);
    .card-border-top-#{$var} {
        border-top: 4px solid nth($border-color, $i);
    }
}
/*======= Card-Border-Top-color Ends  ======= */

/*======= Card-Border-Left-color starts  ======= */
$card-name:primary,
warning,
default,
danger,
success,
inverse,
info;
@each $var in $card-name {
    $i: index($card-name, $var);
    .card-border-left-#{$var} {
        border-left: 4px solid nth($border-color, $i);
    }
}
/*======= Card-Border-Left-color Ends  ======= */

/*======= Notification Card starts  ======= */
.notification-card {
    text-align: center;    
  
    .card-block {
        > div {
            margin-left: 0;
            margin-right: 0;
        }
        padding: 0;
  
        .notify-icon i {
            font-size: 30px;
        }
  
        .notify-cont {
            padding: 10px 0;
            border-left: 1px solid $default-color;
  
            p {
                margin-bottom: 0;
            }
        }
    }
  }
  /*======= Notification Card ends  ======= */