.profile-icon { 
    font-size: 20px;
    color:$primary-active-color;
    }

.profile-location {
    color: $theme-dark-font-color2;
    margin: 0;
    padding: 0;
    line-height: 24px;
    transition: .35s;
}

.profile-intro-header {
    margin-bottom: 0;
    color: $theme-dark-font-color;
    font-size: 14px;
    font-weight: 600;
    margin-right: 10px;
    line-height: 1.4;
}

.profile-intro-subheader {
    margin-bottom: 0;
    color: $theme-dark-font-color2;
    font-size: 14px;
    margin-right: 10px;
    line-height: 1.4;
}

.profile-card{
    border-radius: 5px 0px 0px 5px;
    background: $white-color;
    margin-bottom: 10px;    
    transition: all 0.3s ease-in-out;
    box-shadow: 0 2px 6px rgba(0,0,0,.08);
    height:250px;}

.profile-card-dark{
    border-radius: 0px 5px 5px 0px;
    background: $primary-active-color;
    box-shadow: 0 2px 6px rgba(0,0,0,.08);
    margin-bottom: 10px;    
    transition: all 0.3s ease-in-out;
    height:250px;

    .profile-card-dark-header {
        background-color: transparent;
        padding: 20px 20px 10px 20px;
  
        .profile-card-dark-header-left {
            display: inline-block;
        }
  
        .profile-card-dark-header-right {
            border-radius: 0 0 0 7px;
            right: 15px;
            top: 14px;
            display: inline-block;
            float: right;
            position: absolute;
            @media only screen and (max-width: 575px) {
                display: none;              
            }
  
            i {
                margin: 0 10px;
                cursor: pointer;
                font-size: 20px;
                color: $white-color;
                line-height: 1.4;
                &.icofont.icofont-spinner-alt-5 {
                    display: none;                  
                }
            }
        }
  
        span {
          display: block;
          font-size: 12px;
          margin-top: 5px;
      }

      h5 {
        margin-bottom: 0;
        color: $white-color;
        font-size: 16px;
        font-weight: 600;
        display: inline-block;
        margin-right: 10px;
        line-height: 1.4;
    }
    h6 {
        margin-bottom: 0;
        color: $white-color;
        font-size: 12px;
        font-weight: 600;
        display: inline-block;
        margin-right: 10px;
        line-height: 1.4;
    }      
    }

    .profile-card-dark-block {
        padding: 15px 20px 20px 20px;
        font-size: 12px;       
        color:$white-color;

      p {line-height: 1.4; }
  }  
}

.transaction-footer {
    margin-left: 0;
    margin-right: 0;

    > div {
        padding: 13px 0 14px 0;
        font-size: 14px;
        color:$theme-dark-font-color;
        font-weight:600;
        text-align: center;
        cursor: pointer;
        transition: all 0.3s ease-in-out;

        &:hover {
            background-color: #f1f1f1,;
            color:$primary-active-color;
        }
    }
}

.profile-completion-card {
    border-top: 1px solid #e8e9f2;
    border-left: 1px solid #e8e9f2;
    padding: 10px 0px;
    text-align: center;
    background:$white-color;
    font-weight:600;
    font-size:12px;

    &:hover {
        background-color: $theme-dark-color;
        border-top: 1px solid $theme-dark-color;
        border-left: 1px solid $theme-dark-color;
        color:$white-color;
        cursor: pointer;
    }
}

.profile-completion-card-total {
    border-top: 1px solid $primary-active-color;
    border-left: 1px solid $primary-active-color;
    padding: 10px 0px;
    text-align: center;
    background:$primary-active-color;
    font-weight:600;
    font-size:12px;
    color:$white-color;

}

.profile-completion-header {
    margin: 10px 0px;
    color: $primary-active-color;
    font-size: 14px;
    font-weight: 600;
    display: inline-block;
}

.profile-progress {
    display: flex;
    overflow: hidden;
    height: 4px;
    background-color: #e9ecef;
.profile-progress-bar {
    height: 100% !important;
    background-color: $theme-font-color2;
    position: relative;
    
}
.profile-progress-bar-total {
    height: 100% !important;
    background-color: $secondary-color;
    position: relative;
    
}
}

.score-progress {
    display: flex;
    overflow: hidden;
    height: 4px;
    text-align: center;
    background-color: #e9ecef;
    border-radius:10px;
.score-progress-bar {
    height: 100% !important;
    background-color: $primary-active-color;
    border-radius:10px;
    position: relative;    
}
}

.feed-blog {
    border-left: 1px solid #d6d6d6;
    margin-left: 20px;

    li {
        position: relative;
        padding-left: 22px;
        margin-bottom: 50px;
        h6 {
            font-size: 16px;            
            font-weight: 600;
            color: $theme-dark-font-color;
        }
        .feed-btn {
            width: 37px;
            height: 37px;
            position: absolute;
            left: -20px;
            top: -10px;

            i {
                font-size: 14px;  
            }
        }

        

        
    }
}